import React from "react";
import scssObj from "../../App.scss";
import {Link} from "react-router-dom"

class Shenghuo extends React.Component{
  constructor(props) {
    super(props);
    this.state = {
      items:[],
      // sign:false
    }
  }
    componentDidMount() {
        // ,
        document.getElementById("shenghuo").scrollIntoView(true);
        fetch('https://jsonplaceholder.typicode.com/posts')
        .then(response => response.json())
        .then(json => this.setState({items:json})  )
      }
    render(){
        return(
            <div className={scssObj.classlistLayout} id="shenghuo">
            <div className="container">
              <div className="row">
                <div className="col-3">
                <div className= {scssObj.classlist + " list-group"}>
                 <Link to="/keji" className="list-group-item list-group-item-action ">
                    
                  </Link>
                  <Link to="/shenghuo" className="list-group-item list-group-item-action active"></Link>
                  <Link to="/yule" className="list-group-item list-group-item-action"></Link>
                  <Link to="/aihao" className="list-group-item list-group-item-action"></Link>
                  <Link to="/qita" className="list-group-item list-group-item-action disabled"></Link>
                </div> 
                </div>
                <div className="col-7">
                 <div className={scssObj.kejiList}>
                 <div className="jumbotron jumbotron-fluid">
                 {this.state.items.map(item => {
                  return(
                   <div className={scssObj.item + " container"} key={item.id}>
                     <h5 className="display-4">{item.title}</h5>
                     <p className="lead">{item.body}</p>
                   </div>
                    )})   }   
                 </div>
                 </div>
                 </div>
               
              </div>
            </div>
             
             
            </div>
        )
    }
}

export default Shenghuo;


