import React from "react";
import scssObj from "../../App.scss";
import {Link} from 'react-router-dom';

class register extends React.Component{
    register = () => {
        // localStorage.setItem()
        if(this.isNone()){
            let arr = [];
            if(localStorage.user){
                arr = JSON.parse(localStorage.user);
                for(var e in arr){
                    if(document.getElementById("exampleInputEmail1").value.trim() == arr[e].loginName){
                        alert('');
                        this.clear();
                        return;
                    }
                }
            }
            const user = {
                'loginName': document.getElementById("exampleInputEmail1").value,
                'loginPsd':document.getElementById("exampleInputPassword1").value
            };
            arr.push(user);
            localStorage.user = JSON.stringify(arr);
            // alert('');
            // window.history.go(0);
            window.location.href="/"
            this.clear();
        }
    }
    clear(){
        document.getElementById("exampleInputEmail1").value = "";
        document.getElementById("exampleInputPassword1").value = "";
        document.getElementById("exampleInputPassword2").value = "";
    }
    isNone(){
        if(document.getElementById("exampleInputEmail1").value.trim() === ""){
            alert('');
            return false;
        }else if(document.getElementById("exampleInputPassword1").value.trim() === ""){
            alert('');
            return false;
        }else if(document.getElementById("exampleInputPassword2").value.trim() === ""){
            alert('');
            return false;
        }
        return true;
    }
    render(){
        return(
            <div className={scssObj.login}>
            <div className={scssObj.loginUser}>
                <h2></h2>
                <form>
                  <div className={ scssObj.inputGroup + " form-group"}>
                    {/* <label for="exampleInputEmail1">Email address</label> */}
                    <input type="text" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="" />
                  </div>
                  <div className="form-group">
                    {/* <label for="exampleInputPassword1">Password</label> */}
                    <input type="password" className="form-control" id="exampleInputPassword1" placeholder="" />                
                 </div>
                  <div className="form-group">
                    {/* <label for="exampleInputPassword1">Password</label> */}
                    <input type="password" className="form-control" id="exampleInputPassword2" placeholder="" />                
                 </div>
                  
                <button type="button" className="btn btn-primary btn-lg btn-block" onClick={this.register}></button>
                <div className={ scssObj.linkLogin }>? <Link to="/login"></Link></div>
                </form>
            </div>
        </div>
        )
    }
}
export default register;


